export default {
	name: "chat-input",
	data() {
		return {
			inputValue: '',
			flag: false,
			EXPS: []
		}
	},
	mounted() {
		this._loadEmojiData()
	},
	methods: {
		// 表情框
		getEmotionData: function(pageNow, pageSize) {
			return this.EXPS.slice((pageNow - 1) * pageSize, pageSize * pageNow)
		},
		_loadEmojiData: function() {
			uni.request({
				url: '../../../static/emojiDB.json',
				method: 'get'
			}).then(res => {
				var json = eval('(' + res[1].data + ')')
				this.EXPS = json.EXPS.slice(0)
				console.log(json, "获取表情", this.EXPS)
			})
		},
		getFlag: function(data) {
			this.showExpression = data.flag
		},
		// 
		startRecognize: function() {
			var options = {};
			var that = this;
			options.engine = 'iFly';
			that.inputValue = "";
			plus.speech.startRecognize(options, function(s) {
				console.log(s);
				that.inputValue += s;
			}, function(e) {
				console.log("语音识别失败：" + e.message);
			});
		},
		changeFlag: function() {
			console.log("changeFlag通过父组件触发")
			this.flag = !this.flag
			this.$emit('change-flag', {
				flag: this.flag
			})
		},
		sendMessge: function() {
			this.flag = false
			console.log("sendMessge通过父组件触发")
			var that = this;
			if (that.inputValue.trim() == '') {
				that.inputValue = '';
			} else {
				//点击发送按钮时，通知父组件用户输入的内容
				this.$emit('send-message', {
					type: 'text',
					content: that.inputValue
				});
				that.inputValue = '';
			}
		}
	}
}
